<template>
<div id="app" class="container pt-0 p-4">

  <img src="/images/logo.jpg" class="text-center mb-4 logo" alt="">

  <div class="row gx-3 gy-3">
      <div class="col-12 col-sm-6 col-md-3 " v-for="item in users" :key="item.id">
        <div class="p-3 border text-center" :class="[selectedUser && selectedUser.id === item.id ? 'bg-success text-light' : 'bg-light text-dark']">
          <span class="h5">{{ item.firstName }}</span>
          <br />
          <span v-if="!isLoading" class="delete-text text-info h6" @click="deleteOne(item.id)">supprimer</span>
        </div>
      </div>
  </div>

  <div class="row align-items-center justify-content-center mt-5 mb-5">
    <div class="col-12 col-md-4">
      <div class="input-group mb-3">
        <input v-model="form.firstName" type="text" class="form-control" placeholder="FirstName" aria-label="FirstName" aria-describedby="button-addon2">
        <button :disabled="users.length === 10 || isPicked" @click="addNewUser" class="btn btn-outline-secondary" type="button" id="button-addon2">Add New</button>
      </div>
    </div>
  </div>

  <div class="row align-items-center justify-content-center mt-5">
    <div class="col-12 col-md-4 text-center">
      <button @click="pickingUp" type="button" class="btn btn-success" :disabled="isLoading || users.length < 10">
        <div v-if="isLoading">
          <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
          <span class="ml-2">Loading</span>
        </div>
        <span v-else>Picking Up</span>
      </button>
    </div>
  </div>

  <div class="row align-items-center justify-content-center mt-2 mb-5">
    <div class="col-12 col-md-4 text-center">
      <button @click="resetList" type="button" class="btn btn-danger" :disabled="isLoading">Reset List</button>
    </div>
  </div>

  <div class="row gx-3 gy-3">
    <div class="col-12 col-md-3 text-center">
        <div class="row gx-3 gy-3">
          <div class="col-12">
            <h5>Team One</h5>
          </div>
          <div class="col-12" v-for="item in teamOne" :key="item.id">
            <div class="p-3 border text-center bg-light text-dark">{{ item.firstName }}</div>
          </div>
          <div class="col-12 d-flex flex-row align-items-center justify-content-center" v-if="isPicked">
            <img :src="`/images/${teamOneItem}.png`" class="outfit-ball-img" alt="" />
          </div>
        </div>
      </div>
    <div class="col-12 col-md-6"></div>
    <div class="col-12 col-md-3 text-center">
      <div class="row gx-3 gy-3">
        <div class="col-12">
          <h5>Team Two</h5>
        </div>
        <div class="col-12" v-for="item in teamTwo" :key="item.id">
          <div class="p-3 border text-center bg-light text-dark">{{ item.firstName }}</div>
        </div>
        <div class="col-12 d-flex flex-row align-items-center justify-content-center" v-if="isPicked">
          <img :src="`/images/${teamTwoItem}.png`" class="outfit-ball-img" alt="" />
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import userData from './json/users.json'

export default {
  name: 'App',
  components: {},
  data: () => ({
    interval: null,
    isPicked: false,
    isLoading: false,
    users: userData,
    teamOne: [],
    teamTwo: [],
    teamOneItem: '',
    teamTwoItem: '',
    form: {
      firstName: ""
    },
    selectedUser: null,
  }),
  methods: {
    addNewUser() {
      if (this.users.length < 10) {
        const grather = this.users.reduce((a,b) => a.id > b.id ? a : b);
        this.users = [ ...this.users, { id: grather.id + 1, firstName: this.form.firstName} ];
        this.form.firstName = "";
      }
    },
    deleteOne(id) {
      this.users = this.users.filter(item => item.id !== id);
    },
    pickingUp() {
      this.isLoading = true;
      let timeOut = 0;
      let count = 1;
      this.interval = setInterval(() => {
        if(timeOut < 1000) {
          this.selectedUser = this.users[Math.floor(Math.random()*this.users.length)];
        } else {
          if (count <= 10) {
            if (Number.isInteger(count / 2) === false) {
              this.teamOne = [ ...this.teamOne, this.selectedUser ];
            } else {
              this.teamTwo = [ ...this.teamTwo, this.selectedUser ];
            }
            this.users = this.users.filter(item => item.id !== this.selectedUser.id);
            timeOut = 0;
            count ++;
          } else {
            clearInterval(this.interval);
            this.isPicked = true;
            this.isLoading = false;
            this.chooseBallAndOutfit();
          }
        }
        timeOut += 300;
      }, 300);
    },
    chooseBallAndOutfit() {
      let timeOut = 0;
      let arr = ['ball', 'outfit'];
      this.interval = setInterval(() => {
            if(timeOut < 3000) {
              this.teamOneItem = arr[Math.floor(Math.random()*arr.length)];
              if (this.teamOneItem === 'ball') {
                this.teamTwoItem = 'outfit';
              } else {
                this.teamTwoItem = 'ball';
              }
            }
            else {
              clearInterval(this.interval);
            }
        timeOut += 100;
      }, 200);
    },
    resetList() {
      this.selectedUser = null;
      this.users = userData;
      this.teamOne = [];
      this.teamTwo = [];
    }
  },
}
</script>

